import React from "react";
import styles from "../assets/styles/Principal.module.css";
import moba from "../assets/images/historia-moba.jpeg";
import moba1 from '../assets/images/primerMoba.jpg'
import moba2 from '../assets/images/segundoMoba.jpg'
import moba3 from '../assets/images/tercerMoba.png'
import moba4 from '..//assets/images/cuartoMoba.jpg'
import mapa1 from '..//assets/images/mapa1.webp'
import mapa2 from '../assets/images/mapa2.jpg'

function Principal() {
  return (
    <div>
      <h1> Breve Historia</h1>
      <main className={styles.main}>
        <div className={styles.prrafo1}>
          <h2> O que  um jogo MOBA? </h2>
          <p>
            {" "}
            Um MOBA, abreviao de Multiplayer Online Battle Arena,  um
            subgnero de videogames de estratgia em tempo real em que duas
            equipes de jogadores, geralmente 5 jogadores por equipe, competem
            entre si em um campo de batalha predeterminado.
            <br />
            <br/>
            <img src={mapa1} alt="mapa moba" width={'50%'}/> 
             <br/>

            <br />
            Cada jogador controla um nico personagem com um conjunto de
            habilidades nicas que melhoram  medida que o jogo avana. Os
            jogadores tambm podem atualizar seus personagens comprando itens
            com ouro ganho ao completar objetivos ou matar personagens inimigos.{" "}
            <br />
            <br/>
            <img src={mapa2} alt="mapa moba2" width={'50%'}/> 
             <br/>

            <br />
          </p>
          <h2> Mecnica de jogo MOBA </h2>
          <p>
            {" "}
            O objetivo em um jogo MOBA ser destruir a base inimiga, e para isso
            controlamos um heri, que se tornar mais forte  medida que o jogo
            avana. Trata-se de derrotar os heris inimigos e as unidades
            controladas pela IA do outro time. Alm de jogar com nosso heri,
            que possui poderes e habilidades prprias, teremos que trabalhar em
            conjunto com o restante da nossa equipe.  medida que ganhamos jogos
            ganharemos pontos e melhorias para melhorar nosso personagem.
            <br />
            <br />
            Os jogos MOBA baseiam-se fortemente em jogos de RPG e jogos de
            estratgia em tempo real, especialmente pela importncia dada ao
            heri com quem jogamos. Com os pontos de experincia que obtivemos,
            ser necessrio melhorar os poderes ou feitios do nosso personagem,
            que ter um papel muito especfico dentro da equipa.
            <br />
            <br />
            Alm de se familiarizar com os objetivos e estratgias do jogo, 
            preciso focar no desenvolvimento do heri, que ter que trabalhar,
            custe o que custar, com o restante da equipe. Nesse sentido, existem
            diferentes tipos de heris nos jogos MOBA.  verdade que cada jogo
            tem o seu (dependendo do ttulo podem variar de algumas dezenas a
            centenas), mas em geral mantm-se sempre o mesmo tipo de
            personagens, que devem combinar bem para formar uma equipe
            equilibrada.
          </p>
        </div>
        <img src={moba} className={styles.infografia} alt="infografia" />
        <div className={styles.prrafo2}>
          <h2> Histria dos MOBAs </h2>
          
          <p>
            {" "}
            Os jogadores frequentemente debatem a origem do primeiro jogo MOBA.
            Muitos afirmam que o jogo Herzong Zwei da Sega de 1989  o primeiro
            jogo MOBA j criado <br/>
            <br />
            <img src={moba1} alt="primer moba" width={'50%'}/> 
             <br/>
             <br />
             
            Enquanto outros argumentam que o modo "Precinct
            Assault", introduzido no jogo Future Cop: LAPD de 1998,  o primeiro
            MOBA verdadeiro.
            <br />
            <br/>
            <img src={moba2} alt="segundo moba" width={'50%'}/> 
             <br/>
            <br />
      
            Independentemente de quem veio primeiro, a maioria dos jogadores
            concordar que Aeon of Strife, um mapa personalizado de StarCraft, 
            o jogo que melhor iniciou o movimento do gnero antes dos anos 2000.
            <br/>
            <br />
            <img src={moba3} alt="tercer moba" width={'80%'}/> 
            <br />
             <br/>
            Em 1998, a Blizzard Entertainment lanou StarCraft, um de seus jogos
            em tempo real mais vendidos, jogos de estratgia (RTS) para PC. O
            jogo inclua um conjunto de ferramentas de edio de jogos chamado
            StarEdit que permitia aos usurios projetar e criar mapas
            personalizados com regras e jogabilidade personalizadas. O modder de
            StarCraft Aeon64 criou um mapa personalizado chamado Aeon of Strife
            que se tornou imensamente popular entre a comunidade de StarCraft.
            <br/>
            <br />
            <img src={moba4} alt="cuarto moba" width={'80%'}/> 
             <br/>
            <br />
            Muitos dos principais recursos encontrados em Aeon of Strife
            eventualmente se tornaram a estrutura base para todos os MOBAs
            futuros.{" "}
          </p>
        </div>
        <div className={styles.prrafo3}>
          
        </div>
      </main>
    </div>
  );
}

export default Principal;
